<template>
  <div>
      <div v-for="item in tmepListDatas" :key="item.id">
          {{ item.name }}
      </div>
    <my-markdown></my-markdown>
  </div>
</template>

<script>
import myMarkdown from "../assets/md/readme.md";
export default {
  name: "markdown",
  data() {
    return {
      //readme:this.md2html(readme)
      tmepListDatas:[]
    };
  },
  components: {
    myMarkdown
  },
  mounted() {
    let that = this;
    // 拉取该文件夹下所有文件信息
    const filesMD = require.context("@/assets/md", true, /\.md$/);
    const filesMDNames = filesMD.keys();
    const tmepListDatas = [];
    filesMDNames.map(item => {
      const listObj = {};
      const listItemS = item.split("/");
      if (listItemS.length > 0) {
        listObj.name = listItemS[1].replace(".md", "");
        listObj.path = item;
        listObj.children = [];
        listObj.showChild = false;
      }
      return that.tmepListDatas.push(listObj);
    });
  }
};
</script>

